.parking-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: absolute;
  top: 25%;
  left: 20%;
  width: 250px;
  height: max-content;
  padding: 0 20px;
}

.input-group {
  margin-top: 5px;
  margin-bottom: 5px;
  position: relative;

  // width: calc(100% - 5px);
}

input[type="text"],
input[type="email"] {
  width: calc(100% - 1px);
  padding: 14px 20px;
  border-radius: 5px;
  border: none;
  background-color: rgba(
    0,
    0,
    0,
    0.4
  ); /* Setting input background to transparent black */
  margin-bottom: 10px;
  font-size: larger;
}
input[type="text"]::placeholder,
input[type="email"]::placeholder {
  color: #ffffff;
  padding-left: 20px;
  font-family: "Montserrat";
  font-style: normal;
  // text-shadow: 1px 1px 2px white ; /* Add white shadow effect */
}
input[type="text"]:not(:placeholder-shown),
input[type="email"]:not(:placeholder-shown) {
  color: white;
  font-family: "Montserrat";
  font-style: normal;
  padding-left: 30px;
}

button {
  width: 290px;
  margin-bottom: 5px;
  padding: 12px 30px;
  border: none;
  border-radius: 5px;
  background-color: rgb(161, 0, 161, 0.4);
  color: #ffffff;
  text-shadow: 1px 1px 7px white; /* Add white shadow effect */
  font-family: "Montserrat";
  font-style: normal;
  font-size: larger;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background: linear-gradient(
    268.29deg,
    rgba(166, 0, 207, 0.21) 24.2%,
    rgba(143, 151, 220, 0.21) 102.72%
  );
}
.icons {
  position: absolute;
  z-index: 0;
  color: #ffffff;
  top: 20%;
  left: 3%;
}

/* Style the Select component */
.MuiSelect-root {
  width: 290px;
  border-radius: 5px;
  border: none;
  background-color: rgba(0, 0, 0, 0.4);
  margin-bottom: 10px;
  font-size: larger;
  color: white;
  font-style: normal;

  padding-left: 30px;
  & svg {
    color: white;
  }
}

/* Style the placeholder text in the Select */
.MuiSelect-select.MuiSelect-select {
  color: #ffffff;
  font-style: normal;
  padding-left: 20px;
}

/* Style the dropdown menu items */
.MuiList-root {
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  font-style: normal;
}
